@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Chewy&family=PT+Sans&family=Roboto:wght@300&family=Sansita&display=swap);
html {
  box-sizing: border-box;
  font-size: 16px;
  color: #464646;
  font-family: 'Roboto', sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.Login_login__HBoD0 {
  align-items: center;
  background-color: #FEE19D;
  display: flex;
  justify-content: center;
  height: 100vh;
  margin: auto;
  padding: 10px;
  width: 100vw;
}

.Login_loginDiv__1vKxR {
  align-items: center;
  background-color: rgb(254, 250, 243);
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
