}
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #fcf3de;
}

button {
  font-weight: bold;
  border: none;
  background-color: #FEE19D;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-right: 3px;
}
